class LoggerManageMock {
    columns () {
        return [
            {
                prop: 'id',
                label: 'ID',
                align: 'left'
            },
            {
                prop: 'organName',
                label: '',
                align: 'left'
            },
            {
                prop: 'opName',
                label: '',
                align: 'left'
            },
            {
                prop: 'userName',
                label: '',
                align: 'left'
            },
            {
                prop: 'ip',
                label: 'IP',
                width: 140,
                align: 'left'
            },
            {
                prop: 'createTime',
                label: '',
                width: 180,
                align: 'left'
            }
        ]
    }
    arrowColumns () {
        return [
            {
                prop: 'opName',
                label: '',
                align: 'left'
            },
            {
                prop: 'userName',
                label: '',
                align: 'left'
            },
            {
                prop: 'ip',
                label: 'IP',
                align: 'left'
            },
            {
                prop: 'requestPath',
                label: '',
                align: 'left'
            },
            {
                prop: 'requestParam',
                label: '',
                align: 'left'
            },
            {
                prop: 'responseParam',
                label: '',
                align: 'left'
            }
        ]
    }
    getTableData (data) {
        let tableData = []
        tableData = data.map(function (item) {
            item.status = item.status === 0 ? '' : ''
            return item
        })
        return tableData
    }
    loggerRules () {
        return {
            roleName: [{required: true, message: '', trigger: 'blur'}],
            organId: [{required: true, message: '', trigger: 'change'}]
        }
    }
}

export default new LoggerManageMock()



