import Vue from 'vue'

export default {
  isSuccess (data) {
    if (data) {
      return data.code === '0000'
    }
  },
  /**
   *@function http
   * @param response http
   * @param defaultMessage messagemessage
   * @param errorLevel message(info,warning,error)*/
  showErrorMessage (response, defaultMessage = '', errorLevel = 'warning') {

    let messageOption = null
    try {
      let errorCode = response.response.data.error.code
      messageOption = {
        type: errorLevel,
        message: errorCode === '0000' ? defaultMessage : response.response.data.error.message
      }
    } catch (er) {
      messageOption = {
        type: errorLevel,
        message: defaultMessage
      }
    }
    Vue.prototype.$message(messageOption)
  }
}



