import validateRules from '../../scripts/validate-rules'

class UsersManageMock {
  thead () {
    return [
      {
        prop: 'account',
        label: '',
        align: 'left'
      },
      {
        prop: 'roleName',
        label: '',
        align: 'left'
      },
      {
        prop: 'employeeId',
        label: '',
        align: 'left'
      },
      {
        prop: 'nickName',
        label: '',
        align: 'left'
      },
      {
        prop: 'organName',
        label: '',
        align: 'left'
      },
      {
        prop: 'status',
        label: '',
        width: '98',
        align: 'left'
      },
      {
        prop: 'createTime',
        label: '',
        width: '180',
        align: 'left'
      }
    ]
  }

  getTableData (data) {
    let tableData = []
    tableData = data.map(function (item) {
      item.status = item.status === 0 ? '' : ''
      return item
    })
    return tableData
  }

  userRules () {
    return {
      account: validateRules.code,
      organId: [{required: true, type: 'integer', message: '', trigger: 'change'}],
      roleId: [{required: true, type: 'integer', message: '', trigger: 'change'}],
      employeeId: [
        {required: true, message: '', trigger: 'blur'},
        {max: 30, message: '30'},
        {pattern: /^[a-zA-Z0-9]+$/, message: '', trigger: 'blur'}
      ],
      nickName: [
        {required: true, message: '', trigger: 'blur'},
        {max: 30, message: '30'},
        {pattern: /^[0-9a-zA-Z\u4E00-\u9FA5A]+$/, message: '', trigger: 'blur'}
      ],
      cellphone: [
        {required: true, message: '', trigger: 'blur'},
        {pattern: /^1[0-9]{10}$/, message: '', trigger: 'blur'}
      ],
      email: [
        {required: true, message: '', trigger: 'blur'},
        {type: 'email', message: '', trigger: 'blur,change' }
      ]
    }
  }
}

export default new UsersManageMock()



