class InterfaceMock {
    columns () {
        return [
            {
                prop: 'name',
                label: '',
                align: 'center'
            },
            {
                prop: 'organName',
                label: '',
                align: 'center'
            },
            {
                prop: 'token',
                label: 'TOKEN',
                width: 325,
                align: 'center'
            },
            {
                prop: 'url',
                label: 'URL',
                width: 98,
                align: 'center'
            }
        ]
    }
    interfaceRules () {
        return {
            name: [
                {required: true, message: '', trigger: 'blur'},
                {max: 30, message: '3-30', trigger: 'blur'},
                {pattern: /^[a-zA-Z\u4E00-\u9FA5A0-9]+[\w\u4E00-\u9FA5A]*$/, message: '', trigger: 'blur'}
            ],
            organId: [
                {type:'number', required: true, message: '', trigger: 'change'}
            ],
            token: [
                {required: true, message: 'TOKEN', trigger: 'blur'}
            ],
            url: [
                {required: true, message: 'URL', trigger: 'blur'}
            ]
        }
    }
    getTableData (data) {
        let tableData = []
        tableData = data.map(function (item) {
            item.status = !item.status ? '' : ''
            return item
        })
        return tableData
    }
}
export default new InterfaceMock()



