<template lang="pug">
  div.c-resources-dialog_content
    el-row
        el-button(@click="back") 
    div.c-resources-names
        label.title :
        span {{logger.opName}}
    div.c-resources-names
        label.title :
        span {{logger.opUserId}}
    div.c-resources-names
        label.title IP:
        span {{logger.ip}}
    div.c-resources-names
        label.title :
        span {{logger.requestPath}}
    div.c-resources-names
        label.title 
        span {{logger.requestParam}}
    div.c-resources-names
        label.title 
        span {{logger.name}}
</template>
<script>
import LoggerApi from '@system/api/systemconfig/loggermanage.api'

export default {
  data () {
      return {
        logger: {
            id: '',
            organId: '',
            name: '',
            opUserId: '',
            ip: '',
            requestParam: '',
            responseParam: '',
            requestPath: '',
            startTime: '',
            endTime: ''
        }
      }
  },
  methods: {
      back () {
          this.$router.push('/logger_manage')
      }
  },
  created () {
      this.logger.id = this.$route.params.id
      LoggerApi.getLoggerById(this.logger.id).then(
          (res) => {
              this.logger = res
              this.$message('')
          }, (err) => {
              console.log(err)
              this.$message('')
          }
      )
  }
}
</script>
<style lang="scss" scoped>
.c-resources-dialog_content {
    width: 100%;
    overflow: auto;
    height: 100%;
}
.c-resources-names {
    padding-left: 20px;
    font-weight: 100;
    width: 500px;
    line-height: 40px;
    font-size: 14px;
    color: #777777;
}
label.title {
    font-size: 100;
}
</style>



